html {
  box-sizing: border-box;
  font-size: 42.5%;
  /** Reset para REMS  65,5% = 10px de 16px**/
  scroll-behavior: smooth;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  overflow-x: hidden;
}
.sc {
  overflow-y: hidden;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

